@import "../../_globalColor";

/*React-headroom*/
.headroom-wrapper{
  max-height:100px;
}

/* header */

.dark-menu {
  background-color: $darkBackground !important;
  color: $textColorDark;
}

.dark-menu li a {
  color: $textColorDark !important;
}

// .dark-menu li a:hover {
//   background-color: $buttonColor !important;
// }



.dark-menu .logo-name{
  color: $myTitleColorDark !important;
}

.dark-menu .logo-name2{
  color: $myTitleColorDark2 !important;
}

.header {
  background-color: $lightBackground2;
  max-width: 100%;
  padding: 15px 20px;
  margin: 0px auto;
}

.scroll{
  position: fixed;
  top: 0;
  max-width: 100%;
  padding: 15px 20px;
  margin: 0px auto;
  background-color: $lightBackground4;
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
}

.grey-color {
  color: $subTitle;
}

.header li a {
  display: block;
  padding: 15px 20px;
  color: $textColor;
  font-weight: 500;
  font-size: 1.2em;
  text-decoration: none;
}

// .header li a:hover,
.header .menu-btn:hover {
  background-color: $headerHoverBG;
}


.header li a:hover + .header-button-border {
  // border-color: $educationCardBorder;
  height: auto;
  width: 100%;
  border-color: #F4B400;
  -webkit-transition: width 0.4s ease-in-out;
  -moz-transition: width 0.4s ease-in-out;
  -o-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}

.dark-menu li a:hover + .header-button-border {
  // border-color: $educationCardBorder;
  height: auto;
  width: 100%;
  border-color: $myTitleColorDark2;
  -webkit-transition: width 0.4s ease-in-out;
  -moz-transition: width 0.4s ease-in-out;
  -o-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}

.header-button-border {
  width: 0%;
  border-top: 2px solid $lightBorder1;
  // margin-bottom: 20px;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.header .logo {
  display: block;
  flex: none;
  float: left;
  font-size: 1.5em;
  text-decoration: none;
  font-family: 'Maven Pro', sans-serif;
  // font-family: "Agustina Regular";
  font-weight: 500;
  // margin-top: 10px;
  line-height: normal;
}

.header .logo-name {
  font-variant-ligatures: no-common-ligatures;
  -webkit-font-variant-ligatures: no-common-ligatures;
  // padding: 0 10px;
  color: #0F9D58;
}

.header .left {
  padding-left: 10px;
}

.header .right {
  padding-right: 10px;
}

.header .logo-name2 {
  font-variant-ligatures: no-common-ligatures;
  -webkit-font-variant-ligatures: no-common-ligatures;
  // padding: 0 10px;
  color: #F4B400;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height 0.2s ease-out;
}

/* menu icon */

.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  user-select: none;
}

.header .menu-icon .navicon {
  background: $iconBackground;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;
}

.header .menu-icon .navicon-dark{
  background:$textColorDark;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: $iconBackground;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon-dark:before,
.header .menu-icon .navicon-dark:after {
  background: $textColorDark !important;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 486px;
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent !important;
}

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */

@media (min-width: 48em) {
  .header li {
    float: left;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header .menu {
    max-height: none;
  }
  .header .menu-icon {
    display: none;
  }
}
@media (max-width: 960px) and (min-width: 768px) {
  .header {
    font-size: 12px;
  }
}
